@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600&family=Nunito+Sans:wght@300;400;800&display=swap');

.web__navbar{
    padding: 2rem 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.web__navbar-logo a{
    text-decoration: none;
    color: white;
    font-family: var(--logo-font);
}
.web__navbar-links{
    display: flex;
    gap: 50px;
    word-spacing: 2px;
    font-family: var(--body-font);
}
.web__navbar-links a{
    text-decoration: none;
    color: white;
    transition: .3s;
}
.web__navbar-links a:hover{
    border-bottom: 2px solid gray;
}

.web__home{
    padding: 2rem 4rem;
    font-family: var(--body-font);
}
.web__home-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.content-header{
    padding: 2rem 30rem;
    font-size: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}
.content{
    padding: 4rem 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 230px;
}
.content-header-btn button{
    border: none;
    border-radius: 30px;
    width: 150px;
    height: 50px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    transition: .3s;
}
.content-header-btn button > span{
    font-size: 15px;
}
.content-header-btn button:hover{
    background-color: rgba(255, 255, 255, 0.545);
}
.content-img img{
    width: 600px;
}
.content-news{
    width: 410px;
    font-size: 30px;
}
.content-btn{
    padding-top: 20px;
}
.content-btn button{
    border: none;
    border-radius: 30px;
    width: 150px;
    height: 50px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    transition: .3s;
}
.content-btn button > span{
    font-size: 15px;
}
.content-btn button:hover{
    background-color: rgba(255, 255, 255, 0.545);
}

.content-card{
    padding: 4rem 6rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 200px;
    cursor: pointer;

}
.card-img img{
    width: 700px;
}
.card-content{
    padding-top: 2rem;
}
.card-title{
    font-size: 18px;
}
.card-desc{
    width: 350px;
    font-size: 12px;
    letter-spacing: 2px;
}

.home_brand{
    padding: 15rem 14rem;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center ;
}

.web__what{
    padding: 2rem 4rem;
    background: white;
    color: black;
}

.content__what-btn button{
    border: none;
    background: black;
    color: white;
    border-radius: 30px;
    width: 150px;
    height: 50px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    transition: .3s;
}
.content__what-btn button > span{
    font-size: 15px;
}
.content__what-btn button:hover{
    background-color: rgba(0, 0, 0, 0.449);
}
.content-jobs{
    padding: 4rem 6rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.content-iframe{
    padding: 0 3rem;
    text-align: center;
    font-size: 18px;
    font-family: var(--body-font);
}